import React, {Component} from "react";
import {PropTypes} from "prop-types";
import {connect} from "react-redux";
import {OraButton, OraMessage} from "eshop/components/OraCommonComponents";
import OnlineUtils from "eshop/utils/OnlineUtils";
import OraModal from "eshop/modules/core/components/ui/Modal";
import {getMsisdns, getSimCards, getResourcesMsg, isResourceModalVisible, getResourceModalBundleNo, getEntryForResourceModal, changingMsisdn, changingSimCard, getMsisdn, getSimCard} from "eshop/modules/cart/selectors";
import {fetchMsisdns, fetchSimCards, changeMsisdn, resourceModalClose, changeSimCard, updateResources} from "eshop/modules/cart/actions/resources";
import MsisdnPickerComponent from "./MsisdnPickerComponent";
import SimCardPickerComponent from "./SimCardPickerComponent";
import TransactionProcessType from "../../core/constants/TransactionProcessTypeEnum";

class MsisdnSimCardPickerModal extends Component  {
    constructor (props) {
        super(props);
    }

    save() {
        this.props.updateResources();
    }

    getMsisdnDescription() {
        if (this.props.entry.processType === TransactionProcessType.RETENTION) {
            return 'Przedłużenie dla numeru: '
        }
        return 'Nowy numer: ';
    }

    render(){

        const simCardPickerComponent =  <SimCardPickerComponent fetch={this.props.open}
                                                                simCard={this.props.simCard}
                                                                simCards={this.props.simCards}
                                                                fetchSimCards={this.props.fetchSimCards}
                                                                changeSimCard={this.props.changeSimCard}
                                                                channel={this.props.channel}/>
        const msisdnPickerComponent  =  this.props.entry.processType === TransactionProcessType.ACTIVATION &&
                                        <MsisdnPickerComponent msisdn={this.props.msisdn}
                                                               msisdns={this.props.msisdns}
                                                               fetchMsisdns={this.props.fetchMsisdns}
                                                               changeMsisdn={this.props.changeMsisdn}
                                                               entry={this.props.entry}
                                                               fetch={this.props.open}
                                                               channel={this.props.channel}/>

            return(
                <OraModal id="MsisdnSimCardPickerModal"
                          key="MsisdnSimCardPickerModal"
                          open={this.props.open}
                          onClose={this.props.onClose.bind(this, this.props.bundleNo)}
                          size="narrow">

                    <h2 className="h3 u-margin-l opl-modal-title-space">
                        {this.props.entry.planName}
                    </h2>

                    { this.props.entry.loyaltyLengthDescription && <p>{this.props.entry.loyaltyLengthDescription}</p>}
                    <p>{this.props.entry.simCard && ["Karta SIM (" + this.props.entry.simCard.description + ")"]}
                    {this.props.entry.simCard && this.props.entry.msisdn && [" / "]}
                    {this.props.entry.msisdn && [this.getMsisdnDescription.apply(this), <b>{OnlineUtils.transformMsisdn(this.props.entry.msisdn)}</b>]}</p>
                    {this.props.resourcesMsg && <OraMessage type="error" className="u-spacing-top-l" >{this.props.resourcesMsg.message}</OraMessage>}

                    <div className="o-separator u-margin-l u-margin-top-l"/>

                        {simCardPickerComponent}
                        {msisdnPickerComponent}
                <div className={'u-margin-l l-col l-col-12' + (this.props.entry.processType != TransactionProcessType.MNP ? ' o-separator' : '')}/>
                  <div className="l-row u-no-margin">
                    <div className="l-col--opposite l-col-3 l-col-small-12">
                      <OraButton className="u-w-100" onClick={this.save.bind(this)} aria-label="zmień numer">Zmień</OraButton>
                    </div>
                  </div>
                </OraModal>
            );
    }
}

MsisdnSimCardPickerModal.propTypes = {
     msisdn: PropTypes.string,
     onClose: PropTypes.func,
     processType: PropTypes.string,
     bundleNo: PropTypes.number,
     channel: PropTypes.string
 }

const mapStateToProps = (state) => ({
    msisdns: getMsisdns(state),
    msisdn: getMsisdn(state),
    simCards: getSimCards(state),
    simCard: getSimCard(state),
    resourcesMsg: getResourcesMsg(state),
    open: isResourceModalVisible(state),
    bundleNo: getResourceModalBundleNo(state),
    entry: getEntryForResourceModal(state),
    changingMsisdn: changingMsisdn(state),
    changingSimCard: changingSimCard(state)
});

const mapDispatchToProps = (dispatch) => ({
    fetchMsisdns: (bundleNo) => dispatch(fetchMsisdns(bundleNo)),
    fetchSimCards: (bundleNo) => dispatch(fetchSimCards(bundleNo)),
    changeMsisdn: (msisdn, bundleNo) => dispatch(changeMsisdn(msisdn)),
    changeSimCard: (simCard) => dispatch(changeSimCard(simCard)),
    onClose: (bundleNo) => dispatch(resourceModalClose(bundleNo)),
    updateResources: () => dispatch(updateResources())
})

export default connect(mapStateToProps, mapDispatchToProps)(MsisdnSimCardPickerModal);
